import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-default/index.css'
import Axios from 'axios'
/* eslint-disable no-new */

Vue.use(ElementUI)
Vue.prototype.$http = Axios

// new Vue({
//   el: '#app',
//   router,
//   template: '<App/>',
//   components: { App }
// })

new Vue({
  router: router,
  render: h => h(App)
}).$mount('#app')


